import { useState } from "react";
import { ProductsData } from "../Constants/productsData";
import { filterData, ProductCard } from "../utils/common";
import useOnline from "../utils/useOnline";
import { BUTTON_IMG } from "../utils/common";
import { addItem } from "../utils/cartSlice";

import { addItem } from "../utils/cartSlice";
import { useDispatch } from "react-redux";

import { ProductsData } from "../Constants/productsData";

const Product = () => {
  const [allTshirts, setAllTshirts] = useState(ProductsData);

  const [filteredTshirts] = useState(ProductsData);

  const [searchText, setSearchText] = useState("");
  const dispatch = useDispatch();
  const handleAddItem = (card) => {
    dispatch(addItem(card));
  };

  const isOnline = useOnline();

  if (!isOnline) {
    return (
      <h1 className="text-center text-5xl bg-slate-300 p-10 h-screen">
        You are offline, Check Your Internet Connection ðŸ”˜
      </h1>
    );
  }

  return (
    <>
      <div className="p-5 bg-black my-20">
        <div>
          <input
            type="text"
            className="p-2 rounded "
            placeholder="search"
            value={searchText}
            onChange={(e) => {
              setSearchText(e.target.value);
            }}
          />

          <button
            className="text-white ml-2 p-2 rounded bg-[#a496d7]"
            onClick={() => {
              //need to filter the data
              const data = filterData(searchText, filteredTshirts);
              //update the state - restaurant
              setAllTshirts(data);
            }}
          >
            Search
          </button>
        </div>

        <div className="flex gap-5 flex-wrap mt-10">
          {allTshirts.map((card) => {
            return (
              <div className=" bg-[#a496d7] rounded-lg p-2" key={card.id}>
                <div className="w-72">
                  <ProductCard {...card} />

                  <div>
                    <button
                      className="py-2 px-1 bg-white  mt-4  rounded-md flex items-center font-semibold shadow-md"
                      onClick={() => handleAddItem(card)}
                    >
                      Add to cart
                      <div className="bg-[#a496d7] rounded-xl mx-1 px-1 ">
                        <img src={BUTTON_IMG} />
                      </div>
                    </button>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
};
export default Product;
